import React from "react";
import { IonIcon } from "@ionic/react";
import {
  textOutline,
  checkmarkCircleOutline,
  imageOutline,
  calendarNumberOutline,
  calendarOutline,
  folderOutline,
  personOutline,
  linkOutline,
} from "ionicons/icons"; // Ionic Icons
import data from "./data.json";
const groupedData = {};

function getIconByType(type) {
  switch (type) {
    case "text":
      return textOutline;
    case "boolean":
      return checkmarkCircleOutline;
    case "image":
      return imageOutline;
    case "number":
      return calendarNumberOutline;
    case "date":
      return calendarOutline;
    case "file":
      return folderOutline;
    case "user":
      return personOutline;
    default:
      return textOutline; // Varsaylan olarak text ikonunu gster
  }
}
// JSON verisini gruplama
Object.keys(data).forEach((key) => {
  data[key].forEach((item) => {
    if (!groupedData[key]) {
      groupedData[key] = [];
    }
    groupedData[key].push(item);
  });
});

const nodes = [];
const elementsPerRow = 8; // Her satrda ka element olacak
let currentRow = 0; // Mevcut satr numaras

let renderedKeys = []; // Render edilen anahtarlar tutmak iin bir dizi oluturuldu

Object.keys(groupedData).forEach((key, index) => {
  // Sadece "deleted" zellii null olan objeleri al
  const filteredItems = groupedData[key].filter(
    (item) => item.deleted === null
  );

  if (filteredItems.length > 0) {
    const rowIndex = Math.floor(index / elementsPerRow);
    const colIndex = index % elementsPerRow;
    const xPos = colIndex * 300; // X pozisyonunu her bir element iin farkl bir deer olarak ayarlayn
    const yPos = 25 + rowIndex * 600; // Y pozisyonunu her 5 elementte bir artrarak sralamay dzenleyin

    nodes.push({
      id: `div-${key}`, // ID'yi bala gre belirle
      type: "default",
      data: {
        label: (
          <div key={`div-${index}`} className="div">
            {/* Anahtar daha nce render edilmediyse ekle */}
            {!renderedKeys.includes(key) && <h2 className="heading">{key}</h2>}
            <div className="padding_div">
              {filteredItems.map((item, innerIndex) => (
                <div key={`inner-div-${index}-${innerIndex}`} className="test">
                  <div className="spaceBetween">
                    <p>
                      {item.value.includes("custom") || key.includes("custom")
                        ? `${item.display} `
                        : `${item.display} `}
                    </p>
                  </div>

                  <div className="spaceBetween_2">
                    {item.value.startsWith("custom") ||
                    item.value.startsWith("list.custom") ? (
                      <>
                        <p className="link_icon_p">
                          {item.value}{" "}
                          <IonIcon className="icons_link" icon={linkOutline} />{" "}
                        </p>
                        {/* Zincir ikonu */}
                      </>
                    ) : (
                      <>
                        <IonIcon
                          className="icons"
                          icon={getIconByType(item.value)}
                        />
                      </>
                    )}
                  </div>
                </div>
              ))}
            </div>
          </div>
        ),
      },
      position: { x: xPos, y: yPos },
      style: {
        backgroundColor: "#f7f7f7",
        width: "auto",
        minWidth: "150px",
        border: "none",
        fontSize: "8px",
        textAlign: "left",
        borderRadius: "10px",
        padding: "5px",
        display: "flex",
        flexWrap: "wrap",
        alignItems: "center",
        justifyContent: "center",
        sourceHandle: {
          background: "#4caf50",
          width: "8px",
          height: "8px",
          borderRadius: "50%",
        },
      },
    });
    // Eer anahtar daha nce render edilmediyse renderedKeys dizisine ekle
    if (!renderedKeys.includes(key)) {
      renderedKeys.push(key);
    }
  }
});

export default nodes;
