// app.js
import "reactflow/dist/style.css";
import React, { useState } from "react";
import ReactFlow, { MiniMap } from "reactflow";
import defaultNodes from "./nodes.js";
import defaultEdges from "./edges.js";
import data from "./data.json";

const edgeOptions = {
  animated: true,
  style: {
    stroke: "black",
  },
};

const connectionLineStyle = { stroke: "#41cbf2" };

var urlParams = new URLSearchParams(new URL(window.location.href).search);
var idValue = urlParams.get("id");
console.log(idValue);
const dynamically_link = `https://${idValue}.bubbleapps.io/version-test`;
console.log(dynamically_link);

export default function Flow() {
  const [result, setResult] = useState("");

  const handleClick = async () => {
    try {
      const response = await fetch(dynamically_link);
      const data = await response.json();
      setResult(data.result);
    } catch (error) {
      console.error("Sunucu ile iletiim hatas:", error);
      setResult("Bir hata olutu.");
    }
  };

  return (
    <>
      <div>
        <button onClick={handleClick}>Butona Bas</button>
      </div>
      <ReactFlow
        snapToGrid={true}
        defaultNodes={defaultNodes}
        defaultEdges={defaultEdges}
        defaultEdgeOptions={edgeOptions}
        fitView
        style={{
          backgroundColor: "#F4F2FC",
        }}
        connectionLineStyle={connectionLineStyle}
      >
        <MiniMap zoomable pannable />
      </ReactFlow>
    </>
  );
}
